body {
  margin: 0;
  background-color: #111;
}

#container {
  position: relative;
  background-color: #333;
  width: 100%;
  height: 3em;

  #dot {
    position: absolute;
    top: 50%;
    left: 0;
    width: 1em;
    height: 1em;
    margin: -0.5em;
    background-color: #eee;
    border-radius: 50%;
  }
}
